import uniDrawer from "@/components/uni-drawer/uni-drawer.vue"
export default {
	components: {
		uniDrawer
	},
	data() {
		return {
			visible: false,
			items: [{
					value: 'nearest_distant',
					name: '距离最近',
					checked: 'true'
				}, {
					value: 'most_distant',
					name: '距离最远',
				}, {
					value: 'most_type',
					name: '水果种类'
				}, {
					value: 'best_praise',
					name: '好评优先'
				}, {
					value: 'newest',
					name: '最新发布'
				}
			],
		}
	},
	onNavigationBarButtonTap(e) {
		uni.navigateTo({
			url: '/pages/about/about'
		});
	},
	methods: {
		onTapFilter() {
			this.visible = true
		},
		onDrawerClose() {
			this.visible = false
		},
		onSearch() {

		},
		radioChange: function(evt) {
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].value === evt.target.value) {
					this.current = i;
					break;
				}
			}
		}
	}
}
